.chat-input {
  display: flex;
  padding-top: 16px;
  position: relative;

  &__input {
    border: 1px solid rgb(41, 41, 41);
    flex-grow: 1;
    position: relative;
    padding: 6px 20px;
    margin-bottom: 4px;
    max-height: 90px;
    min-height: 26px;
    overflow-y: scroll;
    overflow-x: hidden;
    outline: none;
    font-size: 16px;
    text-align: left;
    white-space: pre-wrap;
  }

  &__placeholder {
    position: absolute;
    left: 21px;
    bottom: 18px;
    pointer-events: none;
    opacity: 0.3;
  }
}